.progress-circle-track{
    fill: transparent;
    stroke: #DAE2E5;
    stroke-width: 26;
}
.progress-circle-indicator {
    fill: transparent;
    stroke: #61fb69;
    stroke-width: 30;
    stroke-dasharray: 0 10000;
    transition: stroke-dasharray .5s ease;
}
.progress-circle {
    margin: 0 auto;
    border-radius: 50%;
    display: block;
}
.progress-circle-text{
    fill: #ffffff;
}
.progress-circle-text-val{
    font-size:3em;
    font-family: 'Poppins', sans-serif;
}
  .progress-circle-text-percent{
    font-size:1em;
    font-family: 'Poppins', sans-serif;
}
  .progress-circle-text-label{
      font-size: 15px;   
}
  .inputWrapperss{       
    color:rgb(255, 255, 255);
    align-items: center;
    text-align: center;
    padding-bottom: 35px;
    justify-content: center;
    display: block;
} 
  .inputLabel{
      align-items: center;
      text-align: center;
      justify-content: center;
      font-size: 1em;
}
  .newCircleValue{
    padding:8px 10px;
    border-radius:4px;
    border:1px solid #ddd;
    width:25%;
    color:rgb(0, 0, 0);
    font-weight: bold;
    align-items: center;
    text-align: center;
    margin-top: 25px;
    font-size: 1em;
}